.sg-wrap {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.sg-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
}

.sg-header h2 {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 2.5rem;
  font-weight: 700;
  background: linear-gradient(135deg, #2a6db9 0%, #f1f5f6 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
  margin: 0;
}

.sg-header h2 svg {
  color: #a6dbea;
  filter: drop-shadow(0 2px 10px rgba(165, 180, 252, 0.35));
}

/* Search + Create */
.sg-actions {
  width: 100%;
  display: grid;
  grid-template-columns: minmax(220px, 1fr) minmax(420px, 2.5fr);
  gap: 12px;
  align-items: center;
}

.sg-search {
  width: 100%;
  padding: 0.9rem 1rem;
  border: 2px solid rgba(255, 255, 255, 0.12);
  border-radius: 0.9rem;
  background: rgba(255, 255, 255, 0.06);
  color: #9ca3af;
  font-size: 0.95rem;
  transition: all 0.25s ease;
  backdrop-filter: blur(10px);
}

.sg-search::placeholder {
  color: #9ca3af;
}

.sg-search:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102,126,234,0.12);
  background: rgba(255,255,255,0.1);
}

.sg-create {
display: grid;
grid-template-columns: 1fr 1.4fr auto;
gap: 12px;
background: rgba(255,255,255,0.06);
border: 1px solid rgba(255,255,255,0.12);
padding: 0.6rem;
border-radius: 0.9rem;
backdrop-filter: blur(10px);
}

.sg-create input {
width: 100%;
padding: 0.75rem 0.9rem;
border: 2px solid rgba(255, 255, 255, 0.12);
border-radius: 0.7rem;
background: rgba(255, 255, 255, 0.06);
color: #9ca3af;
font-size: 0.95rem;
transition: all 0.25s ease;
}

.sg-create input::placeholder {
  color: #9ca3af;
}

.sg-create input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102,126,234,0.12);
  background: rgba(255,255,255,0.1);
}

/* Buttons */
.btn {
  padding: 0.75rem 1.1rem;
  border: none;
  border-radius: 0.8rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.25s ease;
  background: rgba(255,255,255,0.08);
  color: #374151;
  border: 1px solid rgba(255,255,255,0.12);
}

.btn:hover {
  transform: translateY(-2px);
  background: rgba(255,255,255,0.12);
}

.btn.primary {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: #fff;
  border: none;
  box-shadow: 0 8px 20px rgba(16,185,129,0.35);
}

.btn.primary:hover {
  box-shadow: 0 12px 28px rgba(16,185,129,0.45);
}

/* Main split */
.sg-main {
  display: grid;
  grid-template-columns: 1fr 1.2fr;
  gap: 1.25rem;
  align-items: start;
}

/* List side */
.sg-list {
  display: grid;
  gap: 1rem;
}

.sg-card {
  background: rgba(255,255,255,0.08);
  border: 1px solid rgba(255,255,255,0.12);
  border-radius: 1rem;
  position: relative;
  overflow: hidden;
  transition: all 0.25s ease;
}

.sg-card::before {
  content: '';
  position: absolute;
  inset: 0 0 auto 0;
  height: 3px;
  background: linear-gradient(90deg, #667eea, #764ba2);
}

.sg-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 18px 36px rgba(0,0,0,0.18);
  border-color: rgba(255,255,255,0.22);
  background: rgba(255,255,255,0.12);
}

.sg-card.active {
  outline: 2px solid rgba(102,126,234,0.35);
  box-shadow: 0 14px 30px rgba(102,126,234,0.25);
}

.sg-card-body {
  padding: 1rem;
  cursor: pointer;
}

.sg-card-title {
  color: #ffffff;
  font-weight: 700;
  font-size: 1.1rem;
  margin-bottom: 0.25rem;
}

.sg-card-desc {
  color: #9ca3af;
  font-size: 0.95rem;
  margin-bottom: 0.75rem;
  line-height: 1.45;
}

.sg-card-meta {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #6b7280;
  font-size: 0.85rem;
  gap: 0.75rem;
}

.sg-card-actions,
.sg-detail-actions {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 0.5rem;
}

.sg-card-actions .btn,
.sg-detail-actions .btn {
  flex: 1;
  padding: 0.75rem;
  border: none;
  border-radius: 0.75rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

/* JOIN / CREATE (green) */
.sg-card-actions .btn.primary,
.sg-detail-actions .btn.primary {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: #fff;
}
.sg-card-actions .btn.primary:hover,
.sg-detail-actions .btn.primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(16, 185, 129, 0.4);
}

/* LEAVE (red) */
.sg-card-actions .btn:not(.primary),
.sg-detail-actions .btn:not(.primary) {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  color: #fff;
  border: none;
}
.sg-card-actions .btn:not(.primary):hover,
.sg-detail-actions .btn:not(.primary):hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(239, 68, 68, 0.4);
}

/* Details side */
.sg-details {
  align-self: start;
  background: rgba(26, 26, 46, 0.82);
  border: 1px solid rgba(255,255,255,0.12);
  border-radius: 1rem;
  backdrop-filter: blur(16px);
  padding: 1rem;
  height: auto;
  box-shadow:
    0 12px 30px rgba(0,0,0,0.28),
    inset 0 1px 0 rgba(255,255,255,0.08);
}

.sg-empty {
  text-align: center;
  padding: 2.25rem 1rem;
  color: #6b7280;
}

.sg-loading {
  text-align: center;
  padding: 2.25rem 1rem;
  color: #667eea;
  font-weight: 700;
}

.sg-detail-header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 1rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid rgba(255,255,255,0.12);
  margin-bottom: 0.75rem;
}

.sg-name {
  font-size: 1.35rem;
  font-weight: 700;
  color: #fff;
  margin-bottom: 0.25rem;
}

.sg-desc {
  color: #9ca3af;
  font-size: 0.95rem;
  margin-bottom: 0.5rem;
}

.sg-meta-line {
  color: #c7c9d1;
  font-size: 0.9rem;
  margin: 0.15rem 0;
}

.sg-campus {
  color: #667eea;
  font-weight: 700;
}

/* Members */
.sg-members {
  margin-top: 0.75rem;
}

.sg-members-title {
  color: #845add;
  font-size: 1.05rem;
  font-weight: 700;
  margin-bottom: 0.6rem;
}

.sg-member {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.75rem;
  padding: 0.75rem;
  border-radius: 0.75rem;
  border: 1px solid rgba(255,255,255,0.12);
  background: rgba(255,255,255,0.06);
  transition: all 0.2s ease;
  margin-bottom: 0.5rem;
}

.sg-member:hover {
  transform: translateY(-2px);
  background: rgba(255,255,255,0.1);
}

.sg-member-info {
  display: grid;
  gap: 0.2rem;
}

.sg-member-name {
  color: #ffffff;
  font-weight: 700;
  font-size: 0.98rem;
  display: flex;
  align-items: center;
  gap: 0.4rem;
}

.sg-member-name .badge {
  display: inline-flex;
  align-items: center;
  gap: 0.3rem;
  padding: 0.2rem 0.5rem;
  border-radius: 999px;
  font-size: 0.72rem;
  font-weight: 700;
  background: rgba(16, 185, 129, 0.15);
  color: #10b981;
  border: 1px solid rgba(16, 185, 129, 0.35);
}

.sg-member-meta {
  color: #9ca3af;
  font-size: 0.85rem;
}

/* Responsive */
@media (max-width: 900px) {
  .sg-main {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .sg-header { gap: 12px; }
  .sg-actions { grid-template-columns: 1fr; }
  .sg-create { grid-template-columns: 1fr; }
}

@media (max-width: 480px) {
  .sg-header h2 { font-size: 1.6rem; }
  .sg-card-body { padding: 0.85rem; }
  .sg-member { padding: 0.6rem; }
}
