/* ===== Indigo/blue gradient background, only on admin page ===== */
body.admin-bg {
  background: linear-gradient(135deg, #e0e7ff 0%, #c7d2fe 35%, #a5b4fc 100%);
  min-height: 100vh;
  background-attachment: fixed;
  font-family: 'Inter', sans-serif;
}

/* ===== Theme tokens ===== */
:root {
  --admin-accent: #5b6ee5;
  /* indigo */
  --admin-accent-2: #3f87f5;
  /* blue */
  --admin-muted: #6b7280;
  /* gray-500 */
  --admin-border: rgba(17, 24, 39, .12);
  --admin-card-border: rgba(255, 255, 255, .36);
}

/* ===== Layout ===== */
.admin-wrap {
  padding: 28px 24px 60px;
  max-width: 1200px;
  margin: 0 auto;
  color: #111827;
}

/* Sticky glassy header (title + tabs + logout) */
.admin-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
}

.admin-head.glass {
  background: rgba(255, 255, 255, .45);
  backdrop-filter: blur(12px) saturate(160%);
  -webkit-backdrop-filter: blur(12px) saturate(160%);
  border: 1px solid var(--admin-card-border);
  border-radius: 16px;
  padding: 12px 14px;
}

.sticky {
  position: sticky;
  top: 12px;
  z-index: 20;
}

.head-left {
  display: flex;
  align-items: center;
  gap: 14px;
}

.admin-title {
  font-size: clamp(22px, 3vw, 30px);
  font-weight: 800;
  background: linear-gradient(90deg, var(--admin-accent), var(--admin-accent-2));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  margin: 0;
}

/* Tabs */
.admin-tabs {
  display: flex;
  gap: 10px;
}

.tab-btn {
  padding: 10px 16px;
  border-radius: 999px;
  border: 1px solid var(--admin-border);
  background: rgba(255, 255, 255, .85);
  font-weight: 600;
  transition: all .2s ease;
}

.tab-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 8px 20px rgba(17, 24, 39, .07);
}

.tab-btn.active {
  color: #fff;
  background: linear-gradient(135deg, var(--admin-accent), var(--admin-accent-2));
  border: none;
  box-shadow: 0 12px 24px rgba(63, 135, 245, .25);
}

/* Logout button */
.head-actions {
  display: flex;
  align-items: center;
}

.logout-btn {
  height: 40px;
  padding: 0 16px;
  border-radius: 12px;
  font-weight: 700;
  color: var(--admin-accent-2);
  background: transparent;
  border: 2px solid var(--admin-accent-2);
  cursor: pointer;
  transition: all .2s ease;
}

.logout-btn:hover {
  background: rgba(63, 135, 245, .12);
}

/* Glassy filters bar (sticky) */
.admin-filters {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  margin: 14px 0 18px;
}

.admin-filters.glass {
  background: rgba(255, 255, 255, .45);
  backdrop-filter: blur(10px) saturate(160%);
  -webkit-backdrop-filter: blur(10px) saturate(160%);
  border: 1px solid var(--admin-card-border);
  border-radius: 16px;
  padding: 12px;
}

/* Inputs */
.admin-input,
.admin-select {
  height: 44px;
  min-width: 220px;
  padding: 0 14px;
  border-radius: 12px;
  border: 1.5px solid var(--admin-border);
  background: rgba(255, 255, 255, .94);
  backdrop-filter: blur(6px);
}

.admin-select {
  min-width: 180px;
}

.admin-input:focus,
.admin-select:focus {
  border-color: var(--admin-accent);
  box-shadow: 0 0 0 4px rgba(91, 110, 229, .14);
  outline: none;
}

/* Buttons */
.admin-btn {
  height: 44px;
  padding: 0 18px;
  border-radius: 12px;
  font-weight: 700;
  border: none;
  background: linear-gradient(135deg, var(--admin-accent), var(--admin-accent-2));
  color: #fff;
  transition: all .2s ease;
  cursor: pointer;
}

.admin-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 10px 20px rgba(63, 135, 245, .25);
}

.admin-btn.ghost {
  background: transparent;
  color: var(--admin-accent-2);
  border: 2px solid var(--admin-accent-2);
}

.admin-btn.ghost:hover {
  background: rgba(63, 135, 245, .12);
}

/* Error banner */
.admin-error {
  margin-bottom: 12px;
  padding: 10px 14px;
  border-radius: 12px;
  background: #fff1f2;
  border: 1px solid #fecdd3;
  color: #b91c1c;
  font-weight: 700;
}

/* Glassy table card */
.admin-card {
  border-radius: 18px;
  background: rgba(255, 255, 255, .58);
  backdrop-filter: blur(12px) saturate(180%);
  -webkit-backdrop-filter: blur(12px) saturate(180%);
  border: 1px solid var(--admin-card-border);
  overflow: hidden;
  box-shadow: 0 10px 28px rgba(17, 24, 39, .14);
}

/* Table layout */
.thead,
.tr {
  display: grid;
  grid-template-columns: 1.2fr 2.4fr .9fr .9fr .9fr;
  gap: 16px;
}

.thead {
  position: sticky;
  top: 0;
  z-index: 1;
  background: linear-gradient(180deg, rgba(238, 242, 255, .85), rgba(238, 242, 255, .62));
  backdrop-filter: blur(6px) saturate(140%);
  font-weight: 800;
  color: #111827;
  padding: 14px 18px;
  border-bottom: 1px solid var(--admin-border);
}

.tbody {
  display: flex;
  flex-direction: column;
}

.tr {
  padding: 16px 18px;
  border-top: 1px solid var(--admin-border);
  background: rgba(255, 255, 255, .78);
}

.tr:nth-child(odd) {
  background: rgba(255, 255, 255, .9);
}

.tr:hover {
  background: rgba(255, 255, 255, .98);
}

/* Feedback 4-col */
.is-feedback .thead,
.is-feedback .tr {
  grid-template-columns: 1.2fr 3.2fr 1fr 1fr;
}

/* Cells */
.tr strong {
  color: #111827;
  font-weight: 800;
}

.tr p {
  color: var(--admin-muted);
  margin: 4px 0 0;
  line-height: 1.45;
}

/* Row selects */
select {
  width: 100%;
  border-radius: 999px;
  border: 1px solid var(--admin-border);
  background: #fff;
  padding: 0 12px;
  font-weight: 600;
}

select:focus {
  border-color: var(--admin-accent-2);
  box-shadow: 0 0 0 4px rgba(63, 135, 245, .12);
  outline: none;
}

/* Pager */
.admin-pager {
  margin-top: 16px;
  display: flex;
  justify-content: center;
  gap: 14px;
}

.muted {
  color: var(--admin-muted);
}

/* Responsive */
@media (max-width: 980px) {

  .thead,
  .tr {
    grid-template-columns: 1.1fr 2fr 1fr 1fr;
  }

  .is-feedback .thead,
  .is-feedback .tr {
    grid-template-columns: 1.2fr 2.2fr 1fr 1fr;
  }
}

@media (max-width: 680px) {

  .thead,
  .tr,
  .is-feedback .thead,
  .is-feedback .tr {
    grid-template-columns: 1fr 1fr;
  }

  .thead div:nth-child(n+3) {
    display: none;
  }

  .tr>div:nth-child(n+3) {
    display: none;
  }
}